import { baseImgURL } from 'assets/Constant';
import React from 'react';
import { AiOutlineLeft, AiOutlineRight } from 'react-icons/ai';
const AuthHeader = ({
  navigation,
  right = '',
  left='',
  rightAction = () =>{}
}) => {

  const renderRightSide = () => {
    switch (right) {
      case 'skip':
        return (
          <span className='greenChipHeader' onClick={() => { rightAction(); }}>
            Skip <AiOutlineRight size={10}/>
          </span>
        ); 
      default:
        return <></>;
    }
  };

  const renderLeftSide = () => {
    switch (left) {
      case 'login':
        return (
          <span className='greenChipHeader'  onClick={() => { navigation.goBack() }}>
            <AiOutlineLeft size={10}/> Login
          </span>
          );
      case 'back':
        return (
          <span className='greenChipHeader' onClick={() => { navigation.goBack() }}>
            <AiOutlineLeft size={10}/> Back
          </span>
          );
      default:
        return <></>;
    }
  };

  return (
    <div className='mainHeader'>
      <div style={{ flex: 1 ,margin:'auto'}} >
        {renderLeftSide()}
      </div>
      <div style={{ flex: 2, textAlign:'center' }}>
        <img alt="favicon" src={baseImgURL+"mintbox_main_name_logo.svg"} height="24px" />
      </div>
      <div style={{ flex: 1 ,textAlign:"right"}} >
        {renderRightSide()}
      </div>
    </div>
  );
};

export default AuthHeader;
