import db from 'libs/db';
import React from 'react';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CountDown from 'views/mobileView/components/atoms/CountDown';
import AuthHeader from 'views/mobileView/components/molecules/Headers/AuthHeader';
import AdminServices from 'services/api/AdminServices';

export default function LoginOTPScreen(props) {

	const [otp, setOtp] = React.useState('');
	const [invalidOTP, setinvalidOTP] = React.useState(false);
	const [mainOTP, setmainOTP] = React.useState('');
	const { state } = props.location

	React.useEffect(async () => {
		if (state === null || state === undefined) {
			props.history.push('/signup')
		} else {
			sendOTP();
		}
	}, [])
	const validateInput = () => {
		return otp.length === 6
	}

	React.useEffect(() => {
		if(otp.length === 6){
			onNextBtnPressed()
		}
	},[otp])

	const onNextBtnPressed = async () => {
		if (otp === `${mainOTP}` && mainOTP !== '') {
			const userData = {
				name: state.ClientName,
				email: state.Email,
				mobile: state.MobileNumber,
				id: state.ClientId,
				isLogged: true,
			};
			await db.set(db.model.user, JSON.stringify({ userData }));
			props.history.push({
				pathname: '/dashboard',
				state: { ...state }
			})
		} else {
			setinvalidOTP(true);
		}
	}


	const sendOTP = async () => {
		try {
			setmainOTP('')
			const res = await AdminServices.login(state.ClientId).then(res => res)
			if (res.type) {
				if (res.data.Result === 'success') {
					setmainOTP(res.data.otpcode);
				} else {
					toast.error("Try again ...");
				}
			} else {
				toast.error("Try again ...");
			}
		} catch (error) {
			toast.error("Try again ...");
		}
	};

	const resendOTP = () => {
		setinvalidOTP(false);
		setOtp('')
		sendOTP();
	}

	const mask = (text,type) => {
		if(type==='phone'){
			return text.slice(0,3) + '*****' + text.slice(-2)
		} else{
			const a = text.split('@')
			return a[0].slice(0,2) + '*****@' + a[1]
		}
	}
	return (
		<div className="mainContainer">
			<AuthHeader navigation={props.history} left='back' />
			<div className="dataContainer">
				<div className='login-header'>
					<div className="bigHeader">{"Welcome Back, " + state.ClientName + "!"}</div>
					<div className="bodyTextDark" style={{ marginTop: "6px" }}>Please enter OTP received on {mask(state.Email,'email')} and {mask(state.MobileNumber,'phone')}</div>
				</div>
				<FloatingTextInput
					type="tel"
					id="typepass"
					label="OTP"
					style={{ letterSpacing: '16px' }}
					autoFocus
					onChange={(e) => {
						setinvalidOTP(false)
						const n = e.target.value.replace(/\D/g, '')
						setOtp(n)
					}}
					value={otp}
					maxLength={6}
				/>
				{invalidOTP ? (
					<div className="errorMsg">
						Incorrect OTP. Try again!
					</div>
				) : ''}
				<CountDown totalTime={30000} dir="down" onClick={() => { resendOTP() }} />
			</div>
			<div className="login-footer">
				<button className="solidButton" style={{ opacity: validateInput() ? 1 : 0.4 }}
					onClick={() => { validateInput() && onNextBtnPressed() }}>
					Login <AiOutlineRight size={12} />
				</button>
			</div>
		</div>
	)
}
